import React from "react";
import ReactDOM from 'react-dom/client';
// import './index.css';
import {Checkbox, Label, TextInput} from "flowbite-react";
import {Link} from "react-router-dom";
import { useNavigate } from "react-router-dom";

import {information} from "./newuser";
import {UserList, UserNameList, UserPasswordList, GetAllData, UserInformation} from '../main/getdata'
import { useDispatch, useSelector } from "react-redux";
import { update_information,Page_action, LOGIN, LOGIN_ERROR, REGISTER, REGISTER_ERROR } from "../feature/register/registerSlice";





const MainPage = () =>{


    return (

        <nav className="bg-white border-gray-200 px-2 sm:px-4 py-2.5 rounded dark:bg-gray-900">
            <div className="container flex flex-wrap justify-between items-center mx-auto">
                {/*<a href="https://flowbite.com/" className="flex items-center">*/}
                <a className="flex items-center">
                    <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/b/bc/Rice_Owls_logo.svg/1200px-Rice_Owls_logo.svg.png" className="mr-3 h-6 sm:h-9" alt="Flowbite Logo"/>
                    <span
                        className="self-center text-xl font-semibold whitespace-nowrap dark:text-white">RiceBook</span>
                </a>
                <button data-collapse-toggle="navbar-default" type="button"
                        className="inline-flex items-center p-2 ml-3 text-sm text-gray-500 rounded-lg md:hidden hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-200 dark:text-gray-400 dark:hover:bg-gray-700 dark:focus:ring-gray-600"
                        aria-controls="navbar-default" aria-expanded="false">
                    <span className="sr-only">Open main menu</span>
                    <svg className="w-6 h-6" aria-hidden="true" fill="currentColor" viewBox="0 0 20 20"
                        xmlns="http://www.w3.org/2000/svg">
                        <path
                            d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 15a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1z"
                            ></path>
                    </svg>
                </button>

            </div>
            {/*<div class="text-center">*/}
            <div className="float-landing-container">

                <div className="flex flex-col items-center justify-center">
                    <LoginForm />
                    {/*{this.state.show_login?*/}


                </div>

                {/* Sign Up Form*/}
                <div className="flex flex-col items-center justify-center">


                    <Search/>
                </div>
            </div>
            {/* <button className="inline-block px-6 py-2.5 bg-blue-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out">
                <Link to={'/main'} >Next Page</Link>
            </button> */}
            <GetAllData />

        </nav>


    )
}
const LoginForm = () => {

    const navigate = useNavigate();
    const dispatch = useDispatch()


    const handleSubmit = (e) => {
        e.preventDefault()
        let pass = clickLogin()
        if (pass){
            
            dispatch(Page_action({type: LOGIN}))
            navigate('/main')
        }
        else{
            dispatch(Page_action({type: LOGIN_ERROR}))
        }
    }

    const updateUserInformation = (information) => {
        console.log("all information",information.username,information.address.street, information.address.zipcode, information.phone, information.email)
        dispatch(update_information({information}))
        dispatch(Page_action({type:"LOGIN"}))

    }

    const clickLogin = () =>{

        
        let Account = document.getElementById('account1').value
        let Password = document.getElementById("password1").value
        information.Account = Account
        information.Password = Password


        if (UserNameList.includes(Account)) {
            let index = UserNameList.findIndex((name) => name === Account)
            if (Password === UserPasswordList[index]){
                console.log(UserInformation[index])
                updateUserInformation(UserInformation[index])
                return true
            }
        }
        window.alert("Wrong Account or password")
        // dispatch(new_state({state}))
        return false
    }

    return (
        <form className="flex flex-col gap-4" onSubmit={(event)=>{handleSubmit(event)}}>
            <div>
                <div className="mb-2 block">
                    <Label
                        htmlFor="account1"
                        value="Your account name"
                    />
                </div>
                <TextInput
                    id="account1"
                    type="text"
                    placeholder="TomCruise"
                    required={true}

                />
            </div>
            <div>
                <div className="mb-2 block">
                    <Label
                        htmlFor="password1"
                        value="Your password"
                    />
                </div>
                <TextInput
                    id="password1"
                    type="password"
                    required={true}
                />
            </div>


            <div className="flex flex-col justify-center">
                {/*<Link to={'/main'}>*/}
                    <button type="submit"
                            className="inline-block px-6 py-2.5 bg-blue-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out">

                        Login

                    </button>
                {/*</Link>*/}
            </div>

        </form>
    )
}



const Search = () =>  {

    const dispatch = useDispatch((store) => store.register)

    const navigate = useNavigate()
    const handleSubmit = () => {
        let pass = clickSignup()
        // let state = "SIGNUP"
        if (pass){
            dispatch(Page_action({type: REGISTER}))
            // dispatch(new_state({state}))
            navigate('/main')
        }
        else{
            dispatch(Page_action({type: REGISTER_ERROR}))
            window.alert("Account Exists")
        }
        
    }


    const clickSignup = () => {
        const accountInput = document.getElementById('account')
        if (UserNameList.includes(accountInput.value)){
            return false
        }
        const phoneInput = document.getElementById('phone')
        const emailInput = document.getElementById('email2')
        const zipInput = document.getElementById('zip')
        const passwordInput = document.getElementById("password2")
        const information = {
            username:accountInput.value,
            address:{street:passwordInput.value, zipcode:zipInput.value},
            phone: phoneInput.value,
            email:emailInput.value
            
        }
        dispatch(update_information({information}))
        return true
    }

    return (
        <div className="flex flex-col items-center justify-center">
            <form className="flex flex-col gap-2" onSubmit={handleSubmit}>
                <div>
                    <div className="mb-2 block">
                        <Label
                            htmlFor="account"
                            value="Account"
                        />
                    </div>
                    <TextInput
                        id="account"
                        required={true}

                    />
                </div>
                <div>
                    <div className="mb-2 block">
                        <Label
                            htmlFor="zip"
                            value="Zipcode"
                        />
                    </div>
                    <TextInput
                        id="zip"
                        placeholder="77005"
                        required={true}
                        pattern="[0-9]{5}"
                    />
                </div>
                <div>
                    <div className="mb-2 block">
                        <Label
                            htmlFor="Phone"
                            value="Phone Number"
                        />
                    </div>
                    <TextInput
                        id="phone"
                        placeholder="123-456-7890"
                        required={true}
                        pattern="[0-9]{3}-[0-9]{3}-[0-9]{4}"
                    />
                </div>
                <div>
                    <div className="mb-2 block">
                        <Label
                            htmlFor="email2"
                            value="Your email"
                        />
                    </div>
                    <TextInput
                        id="email2"
                        type="email"
                        placeholder="name@flowbite.com"
                        required={true}

                    />
                </div>
                <div>
                    <div className="mb-2 block">
                        <Label
                            htmlFor="password2"
                            value="Your password"
                        />
                    </div>
                    <TextInput
                        id="password2"
                        type="password"
                        required={true}
                    />
                </div>
                <div>
                    <div className="mb-2 block">
                        <Label
                            htmlFor="repeat-password"
                            value="Repeat password"
                        />
                    </div>
                    <TextInput
                        id="repeat-password"
                        type="password"
                        required={true}
                    />
                </div>

                <div className="flex flex-col justify-center">

                    <button  type="submit"
                            className="inline-block px-6 py-2.5 bg-blue-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out">

                        Sign Up
                    </button>
                </div>
            </form>


        </div>
    );
};


export default MainPage;