import React, { useState, useEffect } from 'react';
import './main.css'
import {reducer} from './reducer'
import { useDispatch, useSelector } from 'react-redux';
import { addFollow, unFollow, update } from '../feature/post/postSlice';


const userUrl = "https://jsonplaceholder.typicode.com/users"
const postUrl = "https://jsonplaceholder.typicode.com/posts"
const imgUrl = 'https://upload.wikimedia.org/wikipedia/commons/thumb/2/20/Joe_Biden_official_portrait_2013_cropped.jpg/613px-Joe_Biden_official_portrait_2013_cropped.jpg'




export const UserList = async () => {
    const response = await fetch(userUrl)
    const newUser = await response.json()
    
    return newUser
}
export const PostList = async () => {
    const response = await fetch(postUrl)
    const newPost = await response.json()
    return newPost
}
export let UserNameList = []
export let UserPasswordList = []
export let UserPostList = []
export let CurrentFollower = []
export let UserInformation = []
// const FollowerState = {
