//     headline: '',
    
// }

export const GetAllData = (props) => {

    const  dispatch = useDispatch()
    const {CurrentPost} = useSelector((store)=>store.post)

    const getUser = async () => {
        const newUser = await UserList()
        UserInformation = newUser
        let temp = []
        let index = -100
        const newName = newUser.map((user) => 
        {
            if (user.username === props.login_name) 
                index = user.id - 1

            temp.push(user.address.street)
            return user.username
        }
            
        )

        UserPasswordList = temp
        UserNameList = newName
        if (index !== -100){

            let addinglist = []
            for (let i = 1;i < 4;i++) {
                let newindex = (index + i) %10
                addinglist.push({
                    id:newUser[newindex].id,
                    name: newUser[newindex].name
                })
            }
            dispatch(update({addinglist}))
            CurrentFollower = addinglist
            
        }
        
    }

    const getPost = async () => {
        const newPost = await PostList()
        UserPostList = newPost

        console.log("getpost howabout follower", CurrentFollower)
    }
    

    useEffect(() => {
        
        getUser();
        getPost();
    },[])
    return <></>
}

export const ReadUser = (props) => {
    const [users,setUser] = useState([]) //store all data from url
    const [name,setName] = useState('')
    const [currusers,setCurrusers] = useState([]) // store current follower
    const [nameList,setNameList] = useState([]) //store name of all
    const [usernameList, setUsernameList] = useState([]) // store username of all

    const dispatch = useDispatch((start) => start.post)

    const getUser = async () => {
        const newUser = await UserList()
        let temp = []
        let temp1 = []
        setUser(newUser)
        let index = -100
        const newName = newUser.map((user) => 
        {
            if (user.username === props.login_name){
                index = user.id - 1
            }
            temp.push(user.address.street)
            temp1.push(user.username)
            return user.name
        }
            
        )
        
        UserPasswordList = temp
        UserNameList = temp1
        setNameList(newName)
        setUsernameList(temp1)
        // check the name in name list
        // in the name list -> means that have three follower at first
        if (index !== -100){

            let addinglist = []
            for (let i = 1;i < 4;i++) {
                let newindex = (index + i) %10
                addinglist.push({
                    id:newUser[newindex].id,
                    name: newUser[newindex].name
                })
            }    
            setCurrusers(addinglist)
            dispatch(update({addinglist}))
            CurrentFollower = addinglist
            
        }
        
    }

    const handleClick = (id) => {
        const newUser = currusers.filter((person) => 
            person.id !== id
        )
        
        setCurrusers(newUser)
        dispatch(unFollow({newUser}))
    }

    useEffect(() => {
        
        getUser();
        // getFollower();
    },[])

    useEffect(() => {
        //TODO write dispatch calling function to update article
        CurrentFollower = currusers

    },[currusers])

    const Addnewfollower = () => {
        if (nameList.includes(name)) {
            let id = nameList.findIndex((user) => user === name)+1
            console.log("print",id)


            const newItem = {
                id , name
            }
            const newUser = [...currusers, newItem]
            setCurrusers(newUser)
            dispatch(addFollow({newUser}))
            setName('')

        }
        else {
            alert("User not exists")
            setName('')
        }
    }
    return (
        <div className="text_align_center">
            <div className="grid gap-6 mb-6 md:grid-cols-2">
                {currusers.map((user) => {
                    return (
                        <div id={user.id} key={user.id} className="mb-6 ">

                            
                            <img src={imgUrl} alt='' width="200px"></img>
                            <h1>{user.name}</h1>
                            <h1> Headline</h1>
                            <button className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800"    
                            onClick={()=>handleClick(user.id)}>
                            unfollow
                            </button>
                        </div>
                    )
                
                })}
            </div>
            <div>
                <div className="mb-6">
                    <input type="text" id="follower-input" value={name} onChange={(e) => setName(e.target.value)}
                        className="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"></input>
                </div>

                <button type="button" id="add_follower" onClick={Addnewfollower}
                        className="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">Add
                </button>
            </div>
            
        </div>
    )


}

