import {createSlice} from "@reduxjs/toolkit"

// export const LOAD_TEST = "TEST"
export const LOGIN = "LOGIN"
export const LOGIN_ERROR = "LOGIN_ERROR"
export const LOGOUT = "LOGOUT"
export const REGISTER = "REGISTER"
export const REGISTER_ERROR = "REGISTER_ERROR"


const initialState = {
    Account: "",
    DisplayName: "",
    ZipCode: "",
    Phone: "",
    Email: "",
    Password: "",
    Headline: "Wonderful weather",
    Page_State: "LOGOUT",
    User_ID: "",
    isLogin: false
}

const registerSlice = createSlice({
    name: 'register',
    initialState,
    reducers: {
        update_information: (state, {payload}) => {
            const information = payload.information
            if (information.username !== "")
                state.Account = information.username
            if (information.address.street !== "")
                state.Password = information.address.street
            if (information.address.zipcode !== "")
                state.ZipCode = information.address.zipcode
            if (information.phone !== "")
                state.Phone = information.phone
            if (information.email !== "")                      
                state.Email = information.email
            if (information.id !== "") 
                state.User_ID = information.id
            
        },
        new_status: (state, action) => {
            state.Headline = action.payload
        },
        Logout_clear: (state,actions) => {
            state.Account=""
            state.DisplayName= ""
            state.ZipCode= ""
            state.Phone= ""
            state.Email= ""
            state.Password= ""
            state.Headline= "Wonderful weather"
            state.User_ID = ""
            state.isLogin= false
        },
        Page_action: (state, action) => {
            // console.log("---",action.payload)
            switch(action.payload.type) {  
                case LOGIN:
                    state.Page_State = "LOGIN"
                    state.isLogin = true
                    // console.log("!!",state.Page_State)
                    break
                case LOGIN_ERROR:
                    state.Page_State = "LOGIN_ERROR"
                    state.isLogin = false
                    // console.log("~~",state.Page_State)
                    break
                case LOGOUT:
                    state.Page_State = "LOGOUT"
                    state.isLogin = false
                    // console.log("@@",state.Page_State)
                    break
                case REGISTER:
                    state.Page_State = "REGISTER"
                    state.isLogin = true
                    // console.log("##",state.Page_State)
                    break
                case REGISTER_ERROR:
                    state.Page_State = "REGISTER_ERROR"
                    state.isLogin = false
                    // console.log("%%",state.Page_State)
                    break
                default:
                    break

            }
        }
    }
})

export const {update_information,Logout_clear,new_status,Page_action} = registerSlice.actions
export default registerSlice.reducer;

