export function Feed(props) {
    console.log(props.posts.length)
    const dispatch = useDispatch()
    if (props.posts) {
        const filterPosts = props.posts.filter((post) => {
            if (props.query === "") {
                return post
            }
            else {
                return post.body.includes(props.query) || post.name.includes(props.query)
            }
        })

        return (
            filterPosts.map((post, index) => {
                return (
                <div key={index} className="comment_style rounded-lg back-color">
                        {/* <h1>{article_id}</h1> */}
                        <h1>Username:  {post.name}</h1>
                        {/* <h1>{title}</h1> */}
                        <h1>Article:   {post.body}</h1>
                        <img src="https://www.princeton.edu/sites/default/files/styles/scale_1440/public/images/2022/02/KOA_Nassau_2697x1517.jpg?itok=lA8UuoHt"></img>
                        <div className="float-post-container post_style"> 

                            <button
                                className="text-white w-full bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm  sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800"
                                >Like
                            </button>
                            <button onClick={()=>{
                                dispatch(clickComment({index}))
                                
                            }}
                            className="text-white w-full bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm  sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800"
                            >Comment
                            </button>
                            {/* { console.log(article_id,isComment)} */}
                        </div>
                        <div className=''>
                            { post.isComment && < Comment />}

                        </div>
                    
                </div>
            )})
            
        )
    }
}
